._bg {
    background-color: #fefdc7;
}

._nav {
    flex-direction: column;
    align-items: center;
    min-width: 28%;
    height: 100vh;
    padding: 25px;
    justify-content: center;
    background-image: url('../images/card.jpg');
    background-size: 100%;
    background-repeat: no-repeat;

    @media only screen and (max-width: 640px) {
        width: 100%;
    }
}

._nav__top {
    text-align: center;
    max-width: 100%;
    word-wrap: break-word;
    margin-bottom: 16px;

    h3, ._nav__subtitle {
        color: #333;
        font-size: 3em;
        font-weight: bold;
        text-shadow: 0 0 1px #fff;
        word-wrap: break-word;
    }

    ._nav__subtitle {
        font-size: 0.95em;
        font-style: italic;
    }
}

._nav__bottom {
    img {
        width: 32px;
        height: 32px;
        margin: 0 5px;
    }

    margin-top: 16px;
}

._article {
    flex-grow: 1;
    max-height: 100vh;

    .navbar {
        box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.14);

        .navbar__skills {
            color: #fff;
            transition: 0.3s;
            padding: 5px;
            border-radius: 10px;
            min-width: 80px;

            &:hover {
                background-color: #333;
                scale: 1.05;
                cursor: pointer;
            }
        }

        .navbar__tech {
            width: 48px;
            height: 48px;
        }
    }
}

._projects {
    padding: 15px;
    gap: 10px;
    flex-wrap: wrap;
    overflow-y: auto;

    .card {
        max-width: calc(25% - 10px);
        box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.14);
        position: relative;

        img {
            min-height: 150px;
        }

        a {
            position: absolute;
            bottom: 10px;
        }

        .card-text {
            margin-bottom: 50px;
        }

        @media only screen and (max-width: 640px) {
            max-width: 100%;
        }
    }
}

._full__height {
    min-height: 100vh;
    display: flex;
    flex-direction: row;

    @media only screen and (max-width: 640px) {
        flex-direction: column;
    }
}

textarea.form-control {
    resize: none;
}

.modal {
    background-color: rgba(244, 244, 244, 0.6);

    .modal-content {
        box-shadow: 0 10px 10px 5px rgba(0, 0, 0, 0.14);
    }
}